//Landing page ui
    .landing-top-header {
        background-image: url(../images/LandingImages/bg4.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        top: 0;
        left: 0;
        right: 0;
        position: relative;
        border-radius: 0px;
        &::before {
            position: absolute;
            content: "";
            left: -19%;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            right: 0;
            width: 35%;
            height: 80%;
            // z-index: -1;
            overflow: hidden;
            background-image: url(../images/LandingImages/bg.png);
        }
    }

    .landing-top-header .top {
        border-bottom: 1px solid transparent;
        .nav {
            li {
                margin-left: 0;
                margin-right: 28px;
                a {
                    color: $default-color;
                    font-size: 15px;
                    text-transform: capitalize;
                    &.active {
                        color: $primary-1;
                    }
                }
            }
        }
    }

    @media (max-width: 991px) {
        .landing-page {
            .nav.navbar-nav.nav-pills {
                display: block;
            }
            .navbar-toggler {
                border: 0 !important;
            }
            .navbar.navbar-expand-lg.navbar-light {
                background-color: $white;
            }

            .side-menu__item {
                padding-left: 25px;
            }
            .app-sidebar.horizontal-main {
                background-color: $white !important;
            }
        }
    }

    .landing-top-header .nav-pills .nav-link.active,
    .nav-pills .show > .nav-link {
        background-color: transparent;
        font-weight: bold;
        color: $primary-1;
    }

    .logo-demo {
        padding-top: 0px;
        margin: 15px 0;
    }

    .spacing-top {
        padding-top: 50px;
    }
    .demo-screen-headline h1 {
