            speak: none;
            font-style: normal;
            font-weight: 400;
            font-variant: normal;
            text-transform: none;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        /* Arrows */

        .slick-prev,
        .slick-next {
            font-size: 0;
            line-height: 0;
            position: absolute;
            top: 50%;
            display: block;
            width: 20px;
            height: 20px;
            padding: 0;
            -webkit-transform: translate(0, -50%);
            -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
            cursor: pointer;
            color: transparent;
            border: none;
            outline: none;
            background: transparent;
        }

        .slick-prev {
            &:hover,
            &:focus {
                color: transparent;
                outline: none;
                background: transparent;
            }
        }

        .slick-next {
            &:hover,
            &:focus {
                color: transparent;
                outline: none;
                background: transparent;
            }
        }

        .slick-prev {
            &:hover:before,
            &:focus:before {
                opacity: 1;
            }
        }

        .slick-next {
            &:hover:before,
            &:focus:before {
                opacity: 1;
            }
        }

        .slick-prev.slick-disabled:before,
        .slick-next.slick-disabled:before {
            opacity: 0.25;
        }

        .slick-prev:before,
        .slick-next:before {
            font-family: "feather";
            font-size: 20px;
            padding: 10px;
            line-height: 1;
            opacity: 0.75;
            color: white;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            border: 1px solid rgb(255 255 255 / 30%);
            background: rgb(255 255 255 / 20%);
            box-shadow: 0px 16px 32px rgb(0 0 0 / 20%);
            border-radius: 50px;
        }

        .slick-prev {
            left: -25px;
        }

        [dir="rtl"] .slick-prev {
            right: -25px;
            left: auto;
        }

        .slick-prev:before {
            content: "\e92e";
        }

        [dir="rtl"] .slick-prev:before {
            content: "→";
        }

        .slick-next {
            right: -25px;
        }

        [dir="rtl"] .slick-next {
            right: auto;
            left: -25px;
        }

        .slick-next:before {
            content: "\e92f";
        }

        [dir="rtl"] .slick-next:before {
            content: "<";
        }

        /* Dots */

        .logo-3 {
